#searchform {
  display: flex;
  align-items: center;
  color: #fff;
}
#searchform input {
  width: 140px;
  border: 0;
  background: 0 0;
  margin: 0 20px 0 0;
  padding: 0;
  outline: none;
  text-align: left;
  transition: width 1s ease-in-out;
  display: flex;
  align-items: center;
  color: #fff;
}

/*-----------------------------------------------------------------------------------*/
/*  Menu Header Logo and Menu
/*-----------------------------------------------------------------------------------*/
.header-top {
  height: 100vh;
  background-color: yellow;
}
@media (max-width: 768px) {
  .header-top a {
    font-size: 48px;
  }
}

#menu-top-menu a {
  color: white;
  text-decoration: none;
  display: inline-block;
  position: relative;
  font-family: Inter;
  font-style: normal;
  font-size: 1rem;
  margin: 0 16px;
  font-weight: 700;
  line-height: 24px;
  white-space: nowrap;
}
@media (max-width: 768px) {
  #menu-top-menu a {
    font-size: 48px;
  }
}
#menu-top-menu a:hover:after {
  transform-origin: left top;
  transform: scale(1, 1);
}
#menu-top-menu a::after {
  position: absolute;
  left: 0;
  content: "";
  width: 100%;
  height: 2px;
  background: currentColor;
  bottom: -4px;
  transform: scale(0, 1);
  transition: transform 0.3s;
  transform-origin: right top;
}

/*-----------------------------------------------------------------------------------*/
/*  reset
/*-----------------------------------------------------------------------------------*/
html {
  box-sizing: border-box;
}

*,
*:before,
*:after {
  box-sizing: inherit;
  font-family: "Inter-Regular", sans-serif;
}

body {
  margin: 0;
  padding: 0;
  font-family: "Inter-Regular", sans-serif;
  font-size: 1.6rem;
  line-height: 2;
}

p {
  font-size: 1.8rem;
  line-height: 5rem;
}

h1,
h2,
h3,
h4 {
  font-family: "Inter-Bold", sans-serif;
  font-weight: 900;
}

a {
  text-decoration: none;
}

ul {
  padding: 0;
  margin: 0;
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}

/*-----------------------------------------------------------------------------------*/
/*  html
/*-----------------------------------------------------------------------------------*/
html,
body {
  padding: 0px;
  margin: 0px;
  scroll-behavior: smooth;
}

